import yonghuCenter from '@/views/pages/yonghu/center'
import jiaoshiList from '@/views/pages/jiaoshi/list'
import jiaoshiDetail from '@/views/pages/jiaoshi/formModel'
import jiaoshiAdd from '@/views/pages/jiaoshi/formAdd'
import shenlunceshiList from '@/views/pages/shenlunceshi/list'
import shenlunceshiDetail from '@/views/pages/shenlunceshi/formModel'
import shenlunceshiAdd from '@/views/pages/shenlunceshi/formAdd'
import tijiaoceshiList from '@/views/pages/tijiaoceshi/list'
import tijiaoceshiDetail from '@/views/pages/tijiaoceshi/formModel'
import tijiaoceshiAdd from '@/views/pages/tijiaoceshi/formAdd'
import ceshipingfenList from '@/views/pages/ceshipingfen/list'
import ceshipingfenDetail from '@/views/pages/ceshipingfen/formModel'
