import gangweileixingList from '@/views/pages/gangweileixing/list'
import gangweileixingDetail from '@/views/pages/gangweileixing/formModel'
import gangweileixingAdd from '@/views/pages/gangweileixing/formAdd'
import kaogonggangweiList from '@/views/pages/kaogonggangwei/list'
import kaogonggangweiDetail from '@/views/pages/kaogonggangwei/formModel'
import kaogonggangweiAdd from '@/views/pages/kaogonggangwei/formAdd'
import chengjifenxiList from '@/views/pages/chengjifenxi/list'
import chengjifenxiDetail from '@/views/pages/chengjifenxi/formModel'
import chengjifenxiAdd from '@/views/pages/chengjifenxi/formAdd'
import ziyuanxiazaiList from '@/views/pages/ziyuanxiazai/list'
import ziyuanxiazaiDetail from '@/views/pages/ziyuanxiazai/formModel'
import ziyuanxiazaiAdd from '@/views/pages/ziyuanxiazai/formAdd'
import exampaperList from '@/views/pages/exam/exampaperList'
import examination from '@/views/pages/exam/examination'
import examrecordList from '@/views/pages/exam/examrecordList'
import examfailrecord from '@/views/pages/exam/examfailrecord'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'ziyuanleixingList',
			component: ziyuanleixingList
		}, {
			path: 'ziyuanleixingDetail',
			component: ziyuanleixingDetail
		}, {
			path: 'ziyuanleixingAdd',
			component: ziyuanleixingAdd
		}
		, {
			path: 'kaogongziyuanList',
			component: kaogongziyuanList
		}, {
			path: 'kaogongziyuanDetail',
			component: kaogongziyuanDetail
		}, {
			path: 'kaogongziyuanAdd',
			component: kaogongziyuanAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'jiaoshiList',
			component: jiaoshiList
		}, {
			path: 'jiaoshiDetail',
			component: jiaoshiDetail
		}, {
			path: 'jiaoshiAdd',
			component: jiaoshiAdd
		}
		, {
			path: 'shenlunceshiList',
			component: shenlunceshiList
		}, {
			path: 'shenlunceshiDetail',
			component: shenlunceshiDetail
		}, {
			path: 'shenlunceshiAdd',
			component: shenlunceshiAdd
		}
		, {
			path: 'tijiaoceshiList',
			component: tijiaoceshiList
		}, {
			path: 'tijiaoceshiDetail',
			component: tijiaoceshiDetail
		}, {
			path: 'tijiaoceshiAdd',
			component: tijiaoceshiAdd
		}
		, {
			path: 'ceshipingfenList',
			component: ceshipingfenList
		}, {
			path: 'ceshipingfenDetail',
			component: ceshipingfenDetail
		}, {
			path: 'ceshipingfenAdd',
			component: ceshipingfenAdd
		}
		, {
			path: 'gangweileixingList',
			component: gangweileixingList
		}, {
			path: 'gangweileixingDetail',
			component: gangweileixingDetail
		}, {
			path: 'gangweileixingAdd',
			component: gangweileixingAdd
		}
		, {
			path: 'kaogonggangweiList',
			component: kaogonggangweiList
		}, {
			path: 'kaogonggangweiDetail',
			component: kaogonggangweiDetail
		}, {
			path: 'kaogonggangweiAdd',
			component: kaogonggangweiAdd
		}
		, {
			path: 'chengjifenxiList',
			component: chengjifenxiList
		}, {
			path: 'chengjifenxiDetail',
			component: chengjifenxiDetail
		}, {
			path: 'chengjifenxiAdd',
			component: chengjifenxiAdd
		}
		, {
			path: 'ziyuanxiazaiList',
			component: ziyuanxiazaiList
		}, {
			path: 'ziyuanxiazaiDetail',
			component: ziyuanxiazaiDetail
		}, {
			path: 'ziyuanxiazaiAdd',
			component: ziyuanxiazaiAdd
		}
		, {
			path: 'exampaperList',
			component: exampaperList
		}, {
			path: 'examrecordList',
			component: examrecordList
		}, {
			path: 'examfailrecord',
			component: examfailrecord
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
	, {
		path: '/examination',
		component: examination
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
